.select {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 39px;
    font-size: 18px;
    line-height: 24px;
    cursor: pointer;
    user-select: none;

    &_value {
        width: 100%;
        text-align: center;
    }

    &_options {
        position: absolute;
        z-index: 9999;
        top: 39px;
        left: 0;
        right: 0;

        &_item {
            padding: 8px 0;
            cursor: pointer;
            background: white;
            border-top: 2 solid #E9ECEF;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
                background: darken(white, 5);
            }
        }
    }
}