$fullWidth: 543px;
$heightBody: 419px;
$heightHeader: 37px;

.income {
    color: #28C76F;
}

.table {
    width: $fullWidth;

    &_width {
        width: 25%;
    }

    &_edit,
    &_delete {
        cursor: pointer;
        user-select: none;
    }

    &_edit {
        fill: #6E6B7B;

        &:hover {
            fill: darken(#6E6B7B, 15);
        }
    }

    &_delete {
        margin-left: 30px;
        fill: #EB5757;

        &:hover {
            fill: darken(#EB5757, 15);
        }
    }

    &_header {
        height: $heightHeader;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #5E5873;
        background: #F3F2F7;
        border-bottom: 1px solid #E9ECEF;
    }

    &_body {
        width: 530px;
        text-align: center;

        &_scroll {
            overflow-y: auto;
            height: $heightBody;
            width: $fullWidth;
            background: #F8F9FD;
        }
    }

    &_cell {
        height: 59px;
        border-bottom: 1px solid #E9ECEF;
    }

    &_date,
    &_amount {
        font-weight: 600;
        font-size: 14px;
        line-height: 21px;
    }

    &_empty{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}