$fullHeight: 910px;

.home {
    height: $fullHeight;
    // width: 100%;
    width: 1440px;
    display: flex;
    justify-content: space-between;
}

.pockets {
    width: 891px;

    &-wrapper {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 100%;
        width: 70%;
        background: white;
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.05);
    }

    &_content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 835px;
    }

    &_tables {
        display: flex;
        justify-content: space-between;
        margin-right: 21px;
    }
}

.utils {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    &_calendar {
        display: flex;
        justify-content: center;
    }

    .profile {
        display: flex;
        justify-content: center;
        margin-bottom: 32px;

        &_photo {
            height: 50px;
            width: 50px;
            border-radius: 50%;

            &-container {
                height: 50px;
                width: 50px;
                position: relative;
                cursor: pointer;
                margin-right: 19px;
            }

            &_edit {
                &-container {
                    position: absolute;
                    background: rgba(0, 0, 0, 0.5);
                    height: 50px;
                    width: 50px;
                    border-radius: 50%;
                    top: 0;
                    left: 0;
                    display: none;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }

    .total-amount {
        &_title {
            margin-bottom: 32px;
        }
    }
}