.calendar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 283px;

    &_month-switch {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 16px;
        user-select: none;

        &_svg {
            display: block;
            height: 18px;
            width: 18px;
            cursor: pointer;
            border-radius: 50%;
            text-align: center;

            &:hover {
                background: rgba(0, 0, 0, 0.1);
            }

            &:active {
                background: rgba(0, 0, 0, 0.2);
            }
        }

        &_date {
            width: 150px;
            display: flex;
            justify-content: space-around;
        }

        &_month {
            font-weight: 500;
            font-size: 14px;
            line-height: 18px;
            text-align: center;
            color: #5E5873;
            width: 85px;
            height: 18px;
        }

        &_year {
            font-size: 14px;
            line-height: 21px;
            text-align: center;
            width: 35px;
            height: 18px;
        }
    }

    &_week-days {
        display: flex;
        justify-content: space-between;
        width: 283px;
        margin-bottom: 32px;

        &_item {
            width: 38px;
            text-align: center;
            font-size: 12px;
            line-height: 18px;
        }
    }

    &_day {
        font-size: 14px;
        line-height: 21px;
        height: 34px;
        width: 38px;
        border-radius: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        font-size: 14px;
        line-height: 21px;
        color: #6E6B7B;
        user-select: none;

        &.disabled {
            color: #B9B9C3;
        }

        &:not(.select):not(.now):hover {
            background: #F6F6F6;
            border: 1px solid #E6E6E6;
        }

        &.now {
            background: #7367F0;
            color: white;
        }

        &.select {
            border: 2px solid #7367F0;
            border-radius: 18px;
        }
    }

    &-container {
        width: 305px;
    }

    &_title {
        display: flex;
        justify-content: space-between;
        margin-bottom: 32px;
    }

    &_select {
        width: 100px;
    }
}