.input {
    width: 100%;
    border-radius: 5px;
    height: 38px;
    padding: 0 15px;
    border: 1px solid #D8D6DE;
    font-size: 12px;
    line-height: 24px;
    outline: none;

    &:focus {
        border: 1px solid darken(#D8D6DE, 15);
    }

    &_label {
        display: inline-block;
        font-size: 12px;
        line-height: 18px;
        margin-bottom: 4px;
    }
}