.fields {
    &-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        margin-bottom: 26px;
        padding: 0 30px;
    }

    &-tabs {
        margin-bottom: 36px;
    }
}

.input {
    border: none;
    outline: none;
    width: 100%;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
}