.summary {
    width: 303px;
    height: 456px;
    background: #FFFFFF;
    box-shadow: 1px 8px 25px rgba(168, 168, 168, 0.13);
    border-radius: 12px;
    padding: 6px 0;

    &_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 22px;
        padding: 0 27px;
    }

    .table {
        width: 100%;
        padding: 0 27px;

        &_head {
            margin-bottom: 10px;
        }

        &_scroll {
            height: 357px;
            overflow-y: auto;
        }

        &_cell {
            width: 50%;
            height: 35px;
            font-size: 14px;
            line-height: 24px;
        }

        &_category {
            border-right: 1px solid #E9ECEF;
        }

        &_amount {
            text-align: right;
        }

        &_empty {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

.income {
    color: #28C76F;
}