.fields {
    width: 33%;

    &-container {
        margin-bottom: 26px;
    }

    &-top {
        padding: 0 30px 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
        border-bottom: 1px solid #E9ECEF;
    }
}

.input {
    border: none;
    outline: none;
    width: 100%;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
}

.color-field {
    position: absolute;
    top: 0;
    left: calc(100% + 17px);
    height: auto;
    width: 250px;
    border-radius: 12px;
    background: white;
    z-index: 9999999;
    padding: 28px 14px 10px 24px;
    display: flex;
    flex-wrap: wrap;

    &_item {
        display: block;
        width: 39px;
        height: 39px;
        border-radius: 50%;
        cursor: pointer;
        margin-bottom: 14px;
        margin-right: 14px;
        position: relative;

        &.active::after {
            content: '';
            position: absolute;
            top: -4px;
            left: -4px;
            right: -4px;
            bottom: -4px;
            width: 43px;
            height: 43px;
            border: 2px solid black;
            border-radius: 50%;
        }
    }
}

.tabs {
    display: flex;

    &_item {
        height: 41px;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 18px;
        line-height: 24px;
        cursor: pointer;
        user-select: none;

        &.active {
            font-weight: bold;
        }
    }
}