.widgets {

    &-container {
        display: flex;
        height: 329px;
        justify-content: space-between;

        &__reverse {
            flex-wrap: wrap-reverse;
        }
    }

    &-items {
        display: flex;
        flex-wrap: wrap;
        width: 630px;
        height: 329px;

        &.center {
            align-items: center;
            justify-content: center;
        }
    }

    &-item {
        margin-right: 20px;
        margin-bottom: 23px;

        &:nth-child(2n) {
            margin-right: 0;
        }
    }

    &_more {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    &_handler {
        width: 222px;
        height: 153px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-right: 21px;
    }

    &_add,
    &_next {
        width: 100%;
        cursor: pointer;
    }

    &_next {
        height: 153px;
        background: #FFF0E9;
        border-radius: 12px;
        border: none;

        &__reverse {
            transform: rotateY(180deg);
        }
    }

    &_add {
        height: 69px;
        border: 2px dashed #7666F8;
        border-radius: 12px;
        background: transparent;
        font-weight: 500;
        font-size: 21px;
        line-height: 26px;
        color: #7367F0;
    }
}