$purple: #5D5FEF;

.form {
    &_title {
        text-align: center;
    }

    &_errors {
        color: #EB5757;
        list-style: none;
        text-align: center;
        margin-bottom: 20px;
    }

    &_btn {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 53px;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;
        border: none;
        font-weight: bold;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
        background: $purple;
        cursor: pointer;
        user-select: none;

        &:hover {
            background: darken($purple, 5);
        }
    }
}