.widget {
    height: 153px;
    width: 305px;
    border-radius: 12px;
    padding: 21px 26px 32px;
    display: flex;
    position: relative;

    &_left {
        margin-right: 27px;
    }

    &_left,
    &_right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &_delete {
        position: absolute;
        top: 10px;
        right: 11px;
        cursor: pointer;
    }

    &_img {
        width: 66px;
        height: 66px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(0, 0, 0, 0.05);
        border-radius: 50%;
    }

    &_validity {
        font-weight: bold;
        font-size: 14px;
        line-height: 21px;
        text-align: center;
    }

    &_title {
        font-size: 14px;
        line-height: 21px;
    }

    &_sum {
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
    }
}