.total-amount {
    width: 330px;
    height: 328.7px;
    background: #FFFFFF;
    box-shadow: 10px 27px 42px rgba(168, 168, 168, 0.13);
    border-radius: 12px;
    display: flex;
    flex-direction: column;

    &_title {
        text-align: center;
    }

    &_content {
        padding: 40px 28px;
        flex: 1;
    }

    &_juke {
        height: 78px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 1px solid #E9ECEF;

        &_text {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &_row {
        border-radius: 6px;
        margin-bottom: 21px;
        display: flex;
        justify-content: space-between;
        font-size: 18px;
        line-height: 35px;
        height: 35px;
        padding: 0 10px;

        &:nth-child(odd) {
            background: #F8F7FC;
        }

        &:nth-child(even) {
            .total-amount_count {
                color: #5D5FEF;
            }
        }
    }

    &_count {
        font-weight: bold;
    }
}