.tabs {
    display: flex;

    .tabs_item+.tabs_item {
        margin-left: 24px;
    }

    &_item {
        width: 76px;
        height: 28px;
        background: #F8F7FC;
        border-radius: 6px;
        cursor: pointer;
        font-weight: bold;
        font-size: 14px;
        line-height: 24px;
        color: #5D5FEF;
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;

        &__active {
            background: #5D5FEF;
            color: white;
        }
    }
}